
  .homeSkills {
    transform: translateY(-200px);
    /* background-color: hsl(219, 48%, 8%); */
    height: 1500px;
    position: relative;
    margin-bottom: -700px;
    margin-top: -50px;
    padding: 200px;
    box-sizing: border-box;
    perspective: 450px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    overflow-x: hidden;
  }
  .skillsheadinghai{
    color: aqua;
    text-shadow: 5px 2px 4px rgba(0, 255, 255, 0.6);
  }
  .homeSkills > h3 {
    text-align: center;
    color: white;
    margin-bottom: 4vmax;
    font-size: 4vmax;
  }
  .homeCubeSkills {
    width: 200px;
    height: 200px;
    animation: cubeRotation 15s linear infinite alternate;
    transform-style: preserve-3d;
    margin-top: -100px;
  }
  
  .homeCubeSkillsFaces {
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 0 10px aqua;
    position: absolute;
    width: inherit;
    height: inherit;
    opacity: 0.7;
  }
  
  .homeCubeSkillsFaces > img {
    width: inherit;
    height: inherit;
  }
  
  .homeCubeSkillsFace1 {
    transform: translate3d(0, 0, 100px);
  }
  .homeCubeSkillsFace2 {
    transform: rotateY(180deg) translate3d(0, 0, 150px);
  }
  .homeCubeSkillsFace3 {
    transform: rotateY(-90deg) translate3d(0, 0, 150px);
  }
  .homeCubeSkillsFace4 {
    transform: rotateY(90deg) translate3d(0, 0, 150px);
  }
  .homeCubeSkillsFace5 {
    transform: rotateX(90deg) translate3d(0, 0, 150px);
  }
  .homeCubeSkillsFace6 {
    transform: rotateX(-90deg) translate3d(0, 0, 150px);
  }

  .cubeShadow {
    width: 300px;
    height: 40px;
    margin-top: -100px;
    background-color: rgba(255, 255, 255, 0.171);
    border-radius: 50%;
    animation: cubeShadow 1s ease-in-out infinite alternate;
    filter: blur(3px);
    margin-bottom: 100px;
  }

  .homeskillsBox {
    background-color: hsl(250, 100%, 75%);
    padding: 0 2vmax;
    position: absolute;
    display: flex;
    flex-direction: column;
    right: -300px;
    animation-duration: 1s;
    animation-timing-function: linear;
    animation-fill-mode: forwards;
  }
  
  .homeskillsBox > svg {
    font-size: 5vmax;
    color: rgb(255, 255, 255);
    transition: all 0.5s;
    margin: 10px;
  }
  .homeskillsBox > svg:hover {
    transform: scale(1.5);
  }
  .homeYoutube {
    background-color: white;
    padding-top: 4vmax;
    transform: translateY(-200px);
    border-radius: 20% 20% 0 0;
    position: relative;
    margin-bottom: -200px;
    box-sizing: border-box;
    min-height: 1000px;
  }
  
  .homeYoutube > h3 {
    text-align: center;
    color: rgba(22, 20, 20, 0.904);
    padding: 4vmax;
  }
  
  .homeYoutubeWrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 2vmax;
    width: 80%;
    margin: auto;
  }
  
  .home {
    background-color: black;
  }

  @keyframes cubeRotation {
    to {
      transform: rotateY(360deg) rotateX(480deg) rotateZ(720deg);
    }
  }

@keyframes cubeShadow {
    to {
      transform: scale(1.2) translateY(-10px);
    }
  }

  @keyframes homeskillsBoxAnimationOn {
    from {
      right: -300px;
    }
    to {
      right: 40px;
    }
  }
  
  @keyframes homeskillsBoxAnimationOff {
    from {
      right: 40px;
    }
    to {
      right: -300px;
    }
  }